.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.color_menu{
  position: absolute;
  left:0;
  right:0;
  margin-left:auto;
  margin-right:auto;
  width: 250px;
  border: 2px solid black;
  background: white;
  border-radius: 30px;
  
  display: block;
}

#my_canvas {
  border: 3px solid black;
  display: block;
  margin: 0 auto;
  background: #fffacc;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
